.hero-section {
    position: relative;
    height: 100vh;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .hero-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
  }
  
  .hero-content {
    position: relative;
    z-index: 1;
    text-align: center;
    color: white;
    max-width: 800px;
    padding: 20px;
  }
  
  .hero-content h1 {
    font-family: 'Space Grotesk', sans-serif;
    font-size: 3rem;
    color: #00AFFF;
  }
  
  .hero-content h2 {
    font-family: 'Space Grotesk', sans-serif;
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
  
  .hero-content p {
    font-family: 'Inter', sans-serif;
    margin-bottom: 16px;
  }
  
  .hero-content button {
    background-color: #00AFFF;
    color: white;
    padding: 12px 24px;
    font-size: 16px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    margin-top: 20px;
  }
  