/* src/components/Navbar.css */

.navbar {
    position: sticky;
    top: 0;
    z-index: 999;
    background-color: #172A3B;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 40px;
    color: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }
  
  .logo {
    font-weight: bold;
    font-size: 24px;
    color: #00AFFF;
    cursor: pointer;
  }
  
  .nav-links {
    display: flex;
    gap: 24px;
    list-style: none;
    align-items: center;
  }
  
  .nav-links li span {
    cursor: pointer;
    transition: color 0.2s;
  }
  
  .nav-links li span:hover {
    color: #00AFFF;
  }
  
  .nav-links button {
    background-color: #00AFFF;
    color: #fff;
    padding: 8px 18px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-weight: bold;
    transition: background 0.3s;
  }
  
  .nav-links button:hover {
    background-color: #0077aa;
  }
  